*,
*::after,
*::before {
  box-sizing: border-box;
  font-family: Arial;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 1em;
  font-size: clamp(0.5rem, 2.5vmin, 1.5rem);
  background-image: linear-gradient(75deg, #514a9d, #24c6dc);
  background-size: 400%;
  animation: bg-animation linear 20s infinite alternate-reverse;
}

nav {
  margin-top: 0.1em;
  height: 3em;
  width: 100%;
  margin: 0;
  border-bottom: 0.13em solid hsla(0, 0%, 94%, 0.8);
  display: flex;
  justify-content: center;
  margin-bottom: 0.5em;
}

nav h1 {
  margin: 0;
  font-family: sans-serif;
  color: white;
  font-size: 2em;
  font-weight: bolder;
  text-decoration: solid;
}

.title {
  border-radius: 0.2em;
  padding-left: 0.5em;
  padding-right: 0.5em;
  padding-bottom: 0.1em;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.game {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0%;
  z-index: 0;
}
.game-container {
  justify-content: center;
  position: inherit;
  align-items: center;
  width: fit-content;
  border-radius: 0.7em;
}

/* BG ANimation */
@keyframes bg-animation {
  0% {
    background-position: right;
  }
  100% {
    background-position: left;
  }
}

/*  HEADING ANIMATION */
.slide-in-blurred-top {
  -webkit-animation: slide-in-blurred-top 0.7s cubic-bezier(0.23, 1, 0.32, 1)
    both;
  animation: slide-in-blurred-top 0.7s cubic-bezier(0.23, 1, 0.32, 1) both;
}
